/**
 *    API
 * @param {*} url -  
 * @param {*} method - 
 * @param {*} options -  
 * @returns -   
 */
export const createRequest = async (url, method = 'GET', options = {}) => {
  const response = await fetch(`${process.env.REACT_APP_API_URL}${url}`, {
    method,
    ...options,
  });
  if (!response.ok) {
      throw new Error('  ');
    }
  return response;
}






